@import "./normalize.css";

@-moz-keyframes spin {
    100% {
        -moz-transform: rotate(360deg);
    }
}
@-webkit-keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
    }
}
@keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@mixin no-select {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
@mixin spoti-button() {
    background-color: #1db954;
    color: #191414;
    text-decoration: none;
    padding: 10px 30px;
    border: none;
    border-radius: 30px;
    outline: none;
    font-size: 0.8rem;
    font-weight: 600;
    cursor: pointer;

    &:hover {
        padding: 12px 32px;
        box-shadow: 0 4px 8px 0 rgba(29, 185, 84, 0.2), 0 6px 20px 0 rgba(29, 185, 84, 0.29);
    }
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
html {
    width: 100%;
}
body {
    height: 100%;
    width: 100%;
    background-color: #191414;
    color: #1db954;
    font-family: Sans-Serif;
}
#root {
    height: 100%;
    width: 100%;
}

.page-container {
    height: 100%;
    width: 100%;
    display: block;

    .top {
        @media (max-width: 700px) {
            margin-top: 10%;
        }
        margin-top: 5%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .header {
            display: flex;
            flex-direction: column;
            column-gap: 4px;
            justify-content: center;
            align-items: center;
            text-align: center;

            @include no-select();

            .title {
                height: 45px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                text-align: center;
                column-gap: 4px;
                margin-bottom: 0;

                .logo {
                    height: 2.5rem;
                    width: 2.5rem;
                    background: no-repeat;
                    background-position: center;
                    background-image: url("/content/img/icon/Spotify_Icon_RGB_Green.png");
                    background-size: 2.5rem;
                    -webkit-animation: spin 9s linear infinite;
                    -moz-animation: spin 9s linear infinite;
                    animation: spin 9s linear infinite;
                }
            }

            .secondary {
                display: flex;
                small {
                    font-size: 0.7rem;
                    padding: 0;
                    margin: 0;
                    line-height: 0;
                }
            }
        }

        .search-container {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            row-gap: 15px;
            width: 100%;
            margin-top: 1rem;

            input {
                text-align: center;
                @media (max-width: 700px) {
                    width: 50%;
                }
                width: 25%;
                padding: 7px 10px;
                font-size: 1rem;
                border: none;
                border-bottom: 1px solid #1db954;
                outline: none;
                background: transparent;
                color: #1db954;
                transition: border-bottom 0.2s linear;
                transition: width 0.2s linear;

                &:focus {
                    @media (max-width: 700px) {
                        width: 60%;
                    }
                    width: 35%;
                    border-bottom: 2px dotted #1db954;

                    &::placeholder {
                        color: transparent;
                    }
                }

                &::placeholder {
                    color: #1db954;
                    opacity: 1;
                }

                &::-ms-input-placeholder {
                    color: #1db954;
                }

                &::-ms-input-placeholder {
                    color: #1db954;
                }
            }
            .random {
                padding: 7px 10px;
                text-align: center;
                cursor: pointer;
                border: none;
                border-bottom: 1px solid #1db954;
                border-left: 1px solid #1db954;
                border-right: 1px solid #1db954;
                outline: none;
                background: transparent;
                color: #1db954;
                transition: border-bottom 0.2s linear;
                font-size: 0.9rem;
                &:hover {
                    border-bottom: 3px dotted #1db954;
                }
            }
        }

        .auth-container {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            margin-top: 1.5rem;
            background-color: transparent;

            .auth-link {
                @include spoti-button();
            }

            h6 {
                margin: 0;
                margin-top: 1rem;
                font-size: 0.6rem;
            }
        }
    }

    .main {
        height: 100%;
        width: 100%;
        margin-top: 5rem;
        margin-bottom: 5rem;

        .results-container {
            height: 100%;

            .results {
                display: flex;
                justify-content: center;
                flex-direction: column;
                align-items: center;

                .funcs {
                    display: block;
                    @include no-select();
                    button {
                        @include spoti-button();
                        margin: 20px 10px;
                    }
                    span {
                        display: block;
                        text-align: center;
                        font-size: 0.7rem;
                    }
                }

                .error {
                    h5 {
                        margin: 5px;
                        text-align: center;
                    }
                }

                .tracks {
                    width: 100%;

                    @media (min-width: 760px) {
                        width: 80%;
                    }
                    @media (min-width: 1260px) {
                        width: 55%;
                    }

                    ul {
                        margin: 0;
                        padding: 0;
                        list-style-type: none;
                        .track-info {
                            display: flex;
                            flex-direction: row;
                            justify-content: flex-start;
                            align-items: center;
                            width: 100%;

                            &:hover {
                                background-color: #191414;
                                filter: brightness(135%);
                                cursor: default;
                            }

                            .art {
                                width: 64px;
                                @include no-select();
                                img {
                                    width: 64px;
                                    height: 64px;
                                    border-radius: 5px;
                                }
                            }
                            .name {
                                font-size: 0.8rem;

                                @media (min-width: 760px) {
                                    font-size: 0.9rem;
                                }
                                @media (min-width: 1260px) {
                                    font-size: 1rem;
                                }

                                width: 60%;
                                margin-left: 1rem;
                                span {
                                    font-size: 0.7rem;

                                    @media (min-width: 760px) {
                                        font-size: 0.8rem;
                                    }
                                    @media (min-width: 1260px) {
                                        font-size: 0.9rem;
                                    }

                                    display: block;
                                }
                            }

                            .preview {
                                @include no-select();
                                width: 35%;
                                @media (min-width: 760px) {
                                    width: 30%;
                                }
                                @media (min-width: 1260px) {
                                    width: 25%;
                                }
                                padding: 0;
                                button {
                                    @include spoti-button();
                                    width: 100%;
                                    font-size: 0.7rem;
                                }
                            }

                            .duration {
                                width: 8%;
                                @include no-select();

                                font-size: 0.7rem;

                                @media (min-width: 760px) {
                                    font-size: 0.8rem;
                                }
                                @media (min-width: 1260px) {
                                    font-size: 0.9rem;
                                }
                            }

                            li {
                                padding: 1rem;
                            }
                        }
                    }
                }
            }
        }
    }

    .bottom {
        display: block;
        height: 25px;
        width: 100%;
        position: absolute;
        top: 5px;

        footer {
            display: flex;
            flex-direction: row;
            font-size: 0.65rem;
            margin-right: 1.5rem;
            width: 100%;
            text-align: center;

            @include no-select();

            .github {
                margin-right: 0.6rem;
                justify-self: center;
                align-self: center;
                flex-grow: 0;

                button {
                    outline: none;
                    border: none;
                    height: 1.4rem;
                    width: 1.4rem;
                    background: no-repeat;
                    background-position: center;
                    background-image: url("/content/img/icon/GitHub-Mark-Light-64px.png");
                    background-size: 1.4rem;
                    cursor: pointer;
                    &:hover {
                        position: relative;
                        bottom: 1.5px;
                        border-radius: 10px;
                        box-shadow: 0 4px 8px 0 rgba(29, 185, 84, 0.5), 0 6px 20px 0 rgba(29, 185, 84, 0.29);
                    }
                }
            }

            .logout {
                margin-left: 0.6rem;
                justify-self: flex-start;
                align-self: flex-start;
                text-align: left;
                flex-grow: 8;

                button {
                    color: #1db954;
                    outline: none;
                    border: none;
                    background-color: transparent;
                    width: 4.4rem;
                    height: 1.4rem;
                    cursor: pointer;
                    &:hover {
                        position: relative;
                        bottom: 1.5px;
                        border-radius: 10px;
                        background-color: #1db954;
                        color: #191414;
                        box-shadow: 0 4px 8px 0 rgba(29, 185, 84, 0.5), 0 6px 20px 0 rgba(29, 185, 84, 0.29);
                    }
                }
            }

            .info {
                display: flex;
                flex-direction: column;
                flex-grow: 0;
                margin-right: 0.6rem;
                .spoti {
                    font-size: 0.6rem;
                }

                .react {
                    h3 {
                        text-shadow: 1px 1px 2px rgba(29, 185, 84, 0.6);
                        display: inline-block;
                        margin: 0;
                    }
                }
            }
        }
    }
}
